import { Component, OnInit } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { ParkList, States, Park } from '../menu/menu.component'


@Component({
  selector: 'app-np-main',
  templateUrl: './np-main.component.html',
  styleUrls: ['./np-main.component.css']
})
export class NpMainComponent implements OnInit {
  parkUrl: string;
  park: Park;
  stateNames: string[][];

  constructor(private route: ActivatedRoute) { }

  ngOnInit() {
    this.parkUrl = this.route.snapshot.params['name'];
    let parkList = new ParkList();
    this.park = parkList.getPark(this.parkUrl);
    let stateList = new States();

    let state = this.park.getStateShort();
    this.stateNames = stateList.getNameArray(state);
  }
}
