import { Component, OnInit } from '@angular/core';
import * as usStates from '../../assets/data/us-states.json';
import * as usParks from '../../assets/data/us-parks.json';

@Component({
  selector: 'app-menu',
  templateUrl: './menu.component.html',
  styleUrls: ['./menu.component.css']
})
export class MenuComponent implements OnInit {
  parkList = new ParkList();
  menuStyle = MenuStyle;
  menuSort = MenuSort;
  style: MenuStyle;
  sort: MenuSort;

  constructor() {}

  ngOnInit() {
    this.style = this.menuStyle.TABLE;
    this.sort = this.menuSort.DEFAULT;
  }

  changeMenuStyle(clickedStyle: MenuStyle) {
    if (clickedStyle == this.menuStyle.TABLE) {
      this.style = this.menuStyle.TABLE;
    } else {
      this.style = this.menuStyle.GRID;
    }
  }

  changeMenuSort(clickedSort: MenuSort) {
    if (clickedSort == this.menuSort.DEFAULT) {
      this.sort = this.menuSort.DEFAULT;
    } else if (clickedSort == this.menuSort.STATE) {
      this.sort = this.menuSort.STATE;
    } else {
      this.sort = this.menuSort.NAME;
    }
  }

}

export enum MenuStyle {
  GRID,
  TABLE,
}

export enum MenuSort {
  DEFAULT,
  NAME,
  STATE,
}

export class Park {
  parkUrl: string;
  ch:      string;
  en:      string;
  states:  string[];
  npsUrl:  string;
  region:  string;
  
  constructor(
      parkUrl: string = '',
      ch: string = '',
      en: string = '',
      states: string[] = [],
      npsUrl: string = '',
      region: string = '') {
    this.parkUrl = parkUrl;
    this.ch = ch;
    this.en = en;
    this.states = states;
    this.npsUrl = npsUrl;
    this.region = region;
  }

  getParkUrl() {
    return this.parkUrl;
  }

  getNameShortCh() {
    return this.ch;
  }

  getNameCh() {
    return this.ch+ '國家公園';;
  }

  getNameShortEn() {
    return this.en;
  }

  getNameEn() {
    if (this.parkUrl == 'american-samoa') {
      return ' National Park of ' + this.en;
    } else {
      return this.en + ' National Park';
    }
  }

  getStateShort(): string[] {
    return this.states;
  }

  getStateCh(): string[] {
    return [];
  }

  getStateEn(): string[] {
    return [];
  }

  getNpsUrl() {
    return `https://www.nps.gov/${this.npsUrl}/index.htm`;
  }
}

export class ParkList {
  usParks: Map<string, Park>;

  constructor() {
    this.usParks = new Map<string, Park>();
    let parks = usParks['default'];
    for (var park in parks) {
      let parkDetails = new Park(
        park,
        parks[park]['ch'],
        parks[park]['en'],
        parks[park]['states'],
        parks[park]['nps_url'],
        parks[park]['region']
      );
      this.usParks.set(park, parkDetails);
    }
  }

  getLength() {
    return this.usParks.size;
  }

  getPark(park: string) {
    return this.usParks.get(park);
  }

  getParks(sort: MenuSort) {
    function byEnName(a: Park, b: Park) {
      return a.getParkUrl() < b.getParkUrl() ? -1 : 1
    }
    function byState(a: Park, b: Park) {
      return a.getStateShort()[0] < b.getStateShort()[0] ? -1 : 1
    }

    if (sort == MenuSort.DEFAULT) {
      return Array.from( this.usParks.values() );
    } else if (sort == MenuSort.STATE) {
      return Array.from( this.usParks.values() ).sort(byState);
    } else {
      return Array.from( this.usParks.values() ).sort(byEnName);
    }
  }
}

interface StateDetails {
  ch: string;
  en: string;
}

export class States {
  usStates: Map<string, StateDetails>;

  constructor() {
    this.usStates = new Map<string, StateDetails>();
    let states = usStates['default'];
    for (var state in states) {
      let stateDetails: StateDetails = { 
        ch: states[state]['ch'],
        en: states[state]['en']
      };
      this.usStates.set(state, stateDetails);
    }
  }

  getNameCh(name: string) {
    return this.usStates.get(name).ch;
  }

  getNameEn(name: string) {
    return this.usStates.get(name).en;
  }

  getNameArray(names: string[]) {
    return names.map(state => [this.getNameCh(state), this.getNameEn(state)]);
  }
}
