import { BrowserModule } from '@angular/platform-browser';
import { NgModule } from '@angular/core';

import { AppRoutingModule } from './app-routing.module';
import { AppComponent } from './app.component';
import { NavbarComponent } from './navbar/navbar.component';
import { MenuGridComponent } from './menu-grid/menu-grid.component';
import { MenuComponent } from './menu/menu.component';
import { NpMainComponent } from './np-main/np-main.component';
import { Routes, RouterModule } from '@angular/router';
import { HomeComponent } from './home/home.component';
import { NotFoundComponent } from './not-found/not-found.component';
import { MenuTableComponent } from './menu-table/menu-table.component';

const appRoutes: Routes = [
  {'path': '', component: HomeComponent},
  {'path': 'us-national-parks', component: MenuComponent},
  {'path': 'us-national-parks/:name', component: NpMainComponent},
  {'path': 'not-found', component: NotFoundComponent},
  {'path': '**', redirectTo: '/not-found'},
]

@NgModule({
  declarations: [
    AppComponent,
    NavbarComponent,
    MenuGridComponent,
    MenuComponent,
    NpMainComponent,
    HomeComponent,
    NotFoundComponent,
    MenuTableComponent
  ],
  imports: [
    BrowserModule,
    AppRoutingModule,
    RouterModule.forRoot(appRoutes),
  ],
  providers: [],
  bootstrap: [AppComponent]
})
export class AppModule { }
